
body{
  margin: 0 0 0 0;
}
.font{
  font-style:italic;
  font-weight:bold;
  font-size:2em;
  font-family:'Helvetica','Verdana','Monaco',sans-serif;
  -webkit-box-align:  center;
      -ms-flex-align:  center;
          align-items:  center;
}
.header{
  margin: 0%;
  padding: 0;
  width: 100%;
  height: 10vh;
  min-height: 100px;
  background-color: #1B144A ;
  color:#fca400 ;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align:  end;
      -ms-flex-align:  end;
          align-items:  end;
}
.logo{
  width: 150px; 
  height: 100px;
}
.header h1{
  width: 40%;
  -webkit-box-pack: flex-end, space-between;
      -ms-flex-pack: flex-end, space-between;
          justify-content: flex-end, space-between;
  text-align: center;
  font-family:'Helvetica','Verdana','Monaco',sans-serif;
}
.header nav {
  height: 100%;
  width: 30%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 20px;
}
.header nav button{
  color: #fca400;
  background: #1B144A;
  border: 1px solid #fca400;
  border-radius: 5px;
  padding: 10px 20px;
}
.header nav button:hover{
  color: #1B144A;
  background: #fca400;
  border: 1px solid #E5E7E9;
}
.main-component{
  height: 75vh;
  margin: 0%;
  padding: 0%;
  background: #fca400 ;
  font-family:'Helvetica','Verdana','Monaco',sans-serif;
}
.footer{
  margin: 0;
  padding: 0;
  width: 100%;
  height: 10vh;
  min-height: 100px;
  background-color: #1B144A;
  color: #fca400;
}
.footer h2{
  width: 100%;
  height: 80%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

#Room{
    width: 100%;
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
#Room h2{
    font-size: 50px;
    color: #283747;
}
#Room span{
    color: crimson;
